import axios from "axios";
import { endpoints } from "Constants/api";

export const addChargersAndTimings = async (data) => {
  const request = await axios({
    method: "post",
    url: endpoints.chargersAndTimings.ADD_CHARGERS_TIMINGS,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: data,
  });
  return request;
};
export const updateChargesAndTimings = async (body) => {
  const request = await axios({
    method: "put",
    url: endpoints.chargersAndTimings.ADD_CHARGERS_TIMINGS,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: body,
  });
  return request;
};
export const getChargesAndTimings = async (propertyId) => {
  try {
    const url = `${endpoints.chargersAndTimings.GET_CHARGERS_TIMINGS}/${propertyId}`;
    const response = await axios.get(url, {
      headers: {
        // Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return response.data;
  } catch (error) {
    console.error("Error fetching company details:", error);
    throw error;
  }
};
